.header-section {
  max-width: 375px;
  margin: 0 auto;
  padding: 25px 20px;
  display: flex;
  position: relative;
}

.header-logo {
  color: #111;
  letter-spacing: -.42px;
  flex-shrink: 0;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
  position: relative;
}

.header-logo .simply-logo {
  font-style: italic;
}

.header-logo .chocolate-logo {
  width: 21px;
  height: 21px;
  position: absolute;
}

.navigation-header-bar {
  color: #fff;
  letter-spacing: -.28px;
  flex-direction: column;
  gap: 24px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 114%;
  list-style: none;
  display: flex;
}

.navigation-header-bar li:hover, .navigation-header-bar li:focus {
  cursor: pointer;
  color: #fd9222;
}

.navigation-header-bar a {
  transition: var(--animation-duration);
  padding-bottom: 5px;
  position: relative;
  overflow: hidden;
}

.navigation-header-bar a:after {
  content: "";
  width: 0;
  height: 3px;
  background-color: #fd9222;
  transition: width .4s ease-in-out;
  position: absolute;
  bottom: 0;
  left: 0;
}

.navigation-header-bar a:hover:after, .navigation-header-bar a:focus:after {
  width: 100%;
}

.navigation-header-bar a:not(:hover):after {
  width: 0%;
  transition: width .4s ease-in-out, left .4s ease-in-out;
  left: 100%;
}

.header-navigation-bar, .header-icons-bar {
  display: none;
}

.header-social-icons {
  color: #fff;
  width: 24px;
  height: 24px;
}

.header-social-icons:hover, .header-social-icons:focus {
  color: #fd9222;
}

.header-modal-button {
  border: none;
}

.burger-button {
  width: 28px;
  height: 28px;
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
}

.header-modal {
  visibility: hidden;
  width: 60%;
  height: 100%;
  opacity: 0;
  pointer-events: none;
  background-color: #1e1823;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  padding: 140px 20px 20px 30px;
  transition: transform .5s, opacity .5s, visibility 0s linear .5s;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.header-modal-visible {
  visibility: visible;
  opacity: 1;
  pointer-events: all;
  transition: transform .5s, opacity .5s;
  transform: translateX(0);
}

.header-icons-section {
  gap: 18px;
  display: flex;
}

.close-burger-btn {
  color: #fff;
  cursor: pointer;
  width: 28px;
  height: 28px;
  transition: var(--animation-duration);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 20px;
  left: 30px;
}

.fa-solid {
  color: #fff;
  font-size: 35px;
}

.fa-solid:hover, .fa-solid:focus {
  color: #fd9222;
}

